import React, { useEffect, useState } from 'react';
import logo from './resources/logo.png'
import './App.css';
import { metamask } from './utils/MetaMask';
import { BigNumber, ethers } from 'ethers';
import { TsubasaStaking__factory, Tsubasa__factory, ERC20__factory } from './typechain';
import axios from 'axios'

// supress ts warnings for window.ethereum
declare let window: any;

const NFT_ADDRESS = '0x503d73d0008b97ad1516845ef73265e77e6d498f'
const TOKEN_ADDRESS = '0x9cB17b17D20638704ab2Fc060A1C2B6c9D53C56b' // INARI
const STAKING_ADDRESS = '0xab71C468213ae6044c10577664933873960E176c'

const NETWORK_NAME = 'mainnet'
const NETWORK_CHAIN_ID = 1
const OPENSEA_PREFIX = 'https://opensea.io/'
const TOKEN_DECIMAL = '1' + '0'.repeat(6)

const nftDatas: any = {}
const nftLoadingStarted: any = {}

interface Data {
  ownedNftIds: number[]
  stakedNftIds: number[]
  claimableToken: BigNumber
  tokenBalance: BigNumber
}

function App() {

  const signer = new ethers.providers.Web3Provider(window.ethereum, NETWORK_CHAIN_ID).getSigner()
  const stakingContract = TsubasaStaking__factory.connect(STAKING_ADDRESS, signer)
  const tsubasaContract = Tsubasa__factory.connect(NFT_ADDRESS, signer)
  const tokenContract = ERC20__factory.connect(TOKEN_ADDRESS, signer)

  let [address, setAddress] = useState('')
  let [loading, setLoading] = useState(false)
  let [data, setData] = useState({} as Data)
  /* eslint-disable no-unused-vars */
  let [nftState, setNftState] = useState(0)
  let [stakeApproved, setStakeApproved] = useState(false)
  let [stakeLoading, setStakeLoading] = useState(false)
  let [unstakeLoading, setUnstakeLoading] = useState(false)
  let [claimLoading, setClaimLoading] = useState(false)
  let [ownedNftSelection, setOwnedNftSelection] = useState({} as any)
  let [stakedNftSelection, setStakedNftSelection] = useState({} as any)

  const connect = async () => {
    try {
      await metamask.connect()
    } catch (e: any) {
      console.error(e)
      window.alert(e?.message || e || "Connect MetaMask Failed")
    }
  }

  async function loadData(address: string, showLoading = false) {
    if (!address) {
      console.error("loadData with empty address")
      return
    }
    if (showLoading) {
      setLoading(true)
    }
    console.log("loadData start")
    let [ownedNftIds0, stakedNftIds0, claimableToken, tokenBalance, stakeApproved] = await Promise.all([
      tsubasaContract.tokensOfOwner(address, 0, 5000),
      stakingContract.getStakedNftIds(address),
      stakingContract.claimableToken(),
      tokenContract.balanceOf(address),
      stakingContract.stakeApproved(),
    ])
    const ownedNftIds = ownedNftIds0.map(a => a.toNumber())
    const stakedNftIds = stakedNftIds0.map(a => a.toNumber())
    console.log('loadData success', {
      ownedNftIds,
      stakedNftIds,
      claimableToken,
      tokenBalance,
    })
    setStakeApproved(stakeApproved)
    setData({
      ownedNftIds,
      stakedNftIds,
      claimableToken,
      tokenBalance,
    })
    setLoading(false)
  }

  useEffect(() => {
    metamask.setAllowedNetwork(NETWORK_CHAIN_ID, NETWORK_NAME)
    metamask.addListener('account', account => {
      setAddress(account)
      loadData(account, true)
    })
    // connect when page starts
    connect()
  }, [])

  function MetaMask() {
    if (address) {
      return (
        <div>Connected: {address}</div>
      )
    } else {
      return (
        <div>
          <button onClick={connect}>Connect Wallet</button>
        </div>
      )
    }
  }

  function Title() {
    return (
      <div className='title'>Stake Your Tsubasa For INARI!</div>
    )
  }

  async function loadNftData(nftId: number) {
    const tokenURI = await tsubasaContract.tokenURI(nftId)
    // console.log('load json...', tokenURI)
    const res: any = await axios.get(tokenURI)
    const json: any = res.data
    const imageURI = json.image
    const nftData = {
      nftId, // 0
      tokenURI, // https://ipfs.io/ipfs/bafybeibesp2djy547m5hgadul3adxshjhry2d6obnyra5ueepn5bskup7m/0
      json, // {name, image, attributes, ...}
      imageURI, // https://ipfs.io/ipfs/bafybeia5od3mhbzdd67a4caw3fd6bdupwtmwb23nk7ywsrr7sm5u4ofmu4/diva.png
    }
    return nftData
  }

  const safeLoadNftData = async (nftId: number) => {
    if (nftLoadingStarted[nftId]) return
    nftLoadingStarted[nftId] = true
    nftDatas[nftId] = await loadNftData(nftId)
    setNftState(Date.now()) // trigger updates
  }

  function multiSelectOwnedNft(nftId: number) {
    const selected = ownedNftSelection[nftId]
    if (selected) {
      delete ownedNftSelection[nftId]
    } else {
      ownedNftSelection[nftId] = true
    }
    setOwnedNftSelection({ ...ownedNftSelection })
    console.log('select nft', nftId, !selected)
  }

  function multiSelectStakedNft(nftId: number) {
    const selected = stakedNftSelection[nftId]
    if (selected) {
      delete stakedNftSelection[nftId]
    } else {
      stakedNftSelection[nftId] = true
    }
    setStakedNftSelection({ ...stakedNftSelection })
    console.log('select nft', nftId, !selected)
  }

  //   function OpenSeaNFTContent(nftId: number) {
  //     const html = `<nft-card
  // contractAddress="${NFT_ADDRESS}"
  // tokenId="${nftId}" network="${NETWORK}">
  // </nft-card>`
  //     return (
  //       <div dangerouslySetInnerHTML={{ __html: html }} />
  //     )
  //   }

  function NFTContent(nftId: number) {
    let content
    const nftData = nftDatas[nftId]
    if (nftData) {
      content = <img src={nftData.imageURI} alt={`NFT #${nftId}`}></img>
    } else {
      safeLoadNftData(nftId)
      content = 'Loading...'
    }
    return (
      <div className='nft-content'>{content}</div>
    )
  }

  function NFT(nftId: number, owned = true) {
    let content = NFTContent(nftId)
    const href = `${OPENSEA_PREFIX}/assets/${NFT_ADDRESS}/${nftId}`
    let selected
    if (owned) {
      selected = (ownedNftSelection[nftId])
    } else {
      selected = (stakedNftSelection[nftId])
    }
    let clickHandler
    if (owned) {
      clickHandler = () => {
        multiSelectOwnedNft(nftId)
      }
    } else {
      clickHandler = () => {
        multiSelectStakedNft(nftId)
      }
    }
    let className = 'nft'
    if (selected) className += ' selected'
    return (
      <div className={className}>
        <div className='nft-wrapper' onClick={clickHandler}>
          {content}
        </div>
        <div className='nft-name'>
          <a href={href} target="_blank" rel="noreferrer">Tsubasa #{nftId}</a>
        </div>
      </div>
    )
  }

  async function approveStake() {
    setStakeLoading(true)
    try {
      await (await tsubasaContract.setApprovalForAll(STAKING_ADDRESS, true, { gasLimit: 46770 * 1.5 })).wait()
      const approved = await stakingContract.stakeApproved()
      setStakeApproved(approved)
    } catch (e: any) {
      console.error(e)
      alert(e?.message || e || "Approve stake failed")
    }
    setStakeLoading(false)
  }

  async function stakeNfts() {
    setStakeLoading(true)
    try {
      const nftIds = Object.keys(ownedNftSelection)
      console.log('stake', nftIds)
      await (await stakingContract.stakeNfts(nftIds, { gasLimit: 159900 + 150000 * nftIds.length })).wait()
      await loadData(address)
      setOwnedNftSelection({})
    } catch (e: any) {
      console.error(e)
      alert(e?.message || e || "Stake failed")
    }
    setStakeLoading(false)
  }

  async function onClickStakeButton() {
    if (stakeLoading) return
    if (!stakeApproved) {
      approveStake()
    } else {
      stakeNfts()
    }
  }

  async function unstakeNfts() {
    setUnstakeLoading(true)
    try {
      const nftIds = Object.keys(stakedNftSelection)
      console.log('unstake', nftIds)
      await (await stakingContract.unstakeNfts(nftIds, { gasLimit: 600000 + 40000 * nftIds.length })).wait()
      await loadData(address)
      setStakedNftSelection({})
    } catch (e: any) {
      console.error(e)
      alert(e?.message || e || "Unstake failed")
    }
    setUnstakeLoading(false)
  }

  function MyNFTsContent() {
    if (loading) {
      return "Loading..."
    } else if (data && data.ownedNftIds && data.ownedNftIds.length > 0) {
      const selectedCount = Object.keys(ownedNftSelection).length;
      const buttonEnabled = !stakeLoading && (!stakeApproved || selectedCount > 0)
      const buttonText = stakeApproved ? `Stake Selected ${selectedCount} NFTs` : "Approve All Tsubasa Stake"
      return (
        <>
          <div className='nft-container'>
            {data.ownedNftIds.map((id, index) => <div key={index}>{NFT(id, true)}</div>)}
          </div>
          <button disabled={!buttonEnabled} onClick={onClickStakeButton}>{buttonText}{stakeLoading ? "..." : ""}</button>
        </>
      )
    } else {
      return "No NFTs"
    }
  }

  function MyNFTs() {
    return (
      <div>
        <div className='title'>My NFTs</div>
        {MyNFTsContent()}
      </div>
    )
  }

  function StakedNFTsContent() {
    if (loading) {
      return "Loading..."
    } else if (data?.stakedNftIds?.length > 0) {
      const selectedCount = Object.keys(stakedNftSelection).length;
      return (
        <>
          <div className='nft-container'>
            {data.stakedNftIds.map((id, index) => <div key={index}>{NFT(id, false)}</div>)}
          </div>
          <button disabled={selectedCount === 0 || unstakeLoading} onClick={unstakeNfts}>Unstake Selected {selectedCount} NFTs{unstakeLoading ? "..." : ""}</button>
        </>
      )
    } else {
      return "No Staked NFTs"
    }
  }

  function StakedNFTs() {
    return (
      <div>
        <div className='title'>Staked NFTs</div>
        {StakedNFTsContent()}
      </div>
    )
  }

  async function claimToken() {
    setClaimLoading(true)
    try {
      await (await stakingContract.claimToken({ gasLimit: 161050 * 1.5 })).wait()
      await loadData(address)
    } catch (e: any) {
      console.error(e)
      alert(e?.message || e || "Unstake failed")
    }
    setClaimLoading(false)
  }

  function ClaimableToken() {
    let contentHtml = undefined
    if (loading) {
      contentHtml = "Loading..."
    } else if (data?.claimableToken !== undefined) {
      contentHtml = <button onClick={claimToken} disabled={data.claimableToken.lte(0) || claimLoading}>Claim {data.claimableToken.div(TOKEN_DECIMAL).toString()} INARI{claimLoading ? '...' : ''}</button>
    } else {
      contentHtml = "No INARI Claimable"
    }
    return (
      <div>
        <div className='title'>Claim INARI</div>
        {contentHtml}
      </div>
    )
  }

  function TokenBalance() {
    let contentHtml = undefined
    if (loading) {
      contentHtml = "Loading..."
    } else if (data?.tokenBalance !== undefined) {
      contentHtml = <div>{data.tokenBalance.div(TOKEN_DECIMAL).toString()} INARI</div>
    } else {
      contentHtml = "No INARI"
    }
    return (
      <div>
        <div className='title'>INARI Balance</div>
        {contentHtml}
      </div>
    )
  }

  return (
    <div className="App">
      <img src={logo} className="App-logo" alt="logo" />
      {Title()}
      {MetaMask()}
      {MyNFTs()}
      {StakedNFTs()}
      {ClaimableToken()}
      {TokenBalance()}
    </div>
  )
}

export default App;
